// import classes from './Forms.module.css';

const Button = (props) => {
  return (
    <button
      className={`button ${props.className ? props.className : ''}`}
      type={props.type || 'button'}
      onClick={props.onClick}
    >
      {props.children}
    </button>
  );
};
const Input = (props) => {
  const isRequired = props.required ? 'required' : 'false';
  return (
    <input
      className={`button ${props.className ? props.className : ''}`}
      type={props.type || 'text'}
      name={props.name}
      id={props.id}
      placeholder={props.placeholder}
      required={isRequired}
    />
  );
};
const TextArea = (props) => {
  const isRequired = props.required ? 'required' : 'false';
  return (
    <textarea
      className={`button ${props.className ? props.className : ''}`}
      type={props.type || 'text'}
      name={props.name}
      id={props.id}
      placeholder={props.placeholder}
      required={isRequired}
      rows={props.rows || ''}
      //   cols={props.cols || ''}
    >
      {props.children}
    </textarea>
  );
};
export { Button, Input, TextArea };
